<template>
  <div>
    <!-- 头部 -->
    <Header />

    <!-- 面包屑 导航信息-->
    <Bread>
      <span>Cart</span>
    </Bread>
    <svg
      style="position: absolute; width: 0; height: 0; overflow: hidden"
      version="1.1"
      xmlns="http://www.w3.org/2000/svg"
      xmlns:xlink="http://www.w3.org/1999/xlink"
    >
      <defs>
        <symbol id="icon-add" viewBox="0 0 32 32">
          <title>add2</title>
          <path
            class="path1"
            d="M15 17h-13.664c-0.554 0-1.002-0.446-1.002-1 0-0.552 0.452-1 1.002-1h13.664v-13.664c0-0.554 0.446-1.002 1-1.002 0.552 0 1 0.452 1 1.002v13.664h13.664c0.554 0 1.002 0.446 1.002 1 0 0.552-0.452 1-1.002 1h-13.664v13.664c0 0.554-0.446 1.002-1 1.002-0.552 0-1-0.452-1-1.002v-13.664z"
          ></path>
        </symbol>
        <symbol id="icon-ok" viewBox="0 0 32 32">
          <title>ok</title>
          <path
            class="path1"
            d="M31.020 0.438c-0.512-0.363-1.135-0.507-1.757-0.406s-1.166 0.435-1.529 0.937l-17.965 24.679-5.753-5.67c-0.445-0.438-1.035-0.679-1.664-0.679s-1.219 0.241-1.664 0.679c-0.917 0.904-0.917 2.375 0 3.279l7.712 7.6c0.438 0.432 1.045 0.681 1.665 0.681l0.195-0.008c0.688-0.057 1.314-0.406 1.717-0.959l19.582-26.9c0.754-1.038 0.512-2.488-0.538-3.233z"
          ></path>
        </symbol>
        <symbol id="icon-edit" viewBox="0 0 32 32">
          <title>edit</title>
          <path
            class="path1"
            d="M28.287 8.51l-4.805-4.806 0.831-0.831c0.472-0.472 1.086-0.777 1.564-0.777 0.248 0 0.452 0.082 0.622 0.253l3.143 3.144c0.539 0.54 0.133 1.529-0.524 2.186l-0.831 0.831zM26.805 9.992l-1.138 1.138-4.805-4.806 1.138-1.138 4.805 4.806zM24.186 12.612l-14.758 14.762-4.805-4.806 14.758-14.762 4.805 4.806zM7.379 28.288l-4.892 1.224 1.223-4.894 3.669 3.67zM31.123 4.011l-3.143-3.144c-0.567-0.567-1.294-0.867-2.103-0.867-1.036 0-2.174 0.52-3.045 1.391l-20.429 20.436c-0.135 0.134-0.23 0.302-0.276 0.487l-2.095 8.385c-0.089 0.355 0.017 0.736 0.276 0.995 0.198 0.198 0.461 0.307 0.741 0.307 0.085 0 0.171-0.010 0.254-0.031l8.381-2.096c0.185-0.047 0.354-0.142 0.487-0.276l20.43-20.436c1.409-1.41 2.042-3.632 0.524-5.15v0z"
          ></path>
        </symbol>
        <symbol id="icon-del" viewBox="0 0 32 32">
          <title>delete</title>
          <path
            class="path1"
            d="M11.355 4.129v-2.065h9.29v2.065h-9.29zM6.194 29.935v-23.742h19.613v23.742h-19.613zM30.968 4.129h-8.258v-3.097c0-0.569-0.463-1.032-1.032-1.032h-11.355c-0.569 0-1.032 0.463-1.032 1.032v3.097h-8.258c-0.569 0-1.032 0.463-1.032 1.032s0.463 1.032 1.032 1.032h3.097v24.774c0 0.569 0.463 1.032 1.032 1.032h21.677c0.569 0 1.032-0.463 1.032-1.032v-24.774h3.097c0.569 0 1.032-0.463 1.032-1.032s-0.463-1.032-1.032-1.032v0z"
          ></path>
          <path
            class="path2"
            d="M10.323 9.806c-0.569 0-1.032 0.463-1.032 1.032v14.452c0 0.569 0.463 1.032 1.032 1.032s1.032-0.463 1.032-1.032v-14.452c0-0.569-0.463-1.032-1.032-1.032z"
          ></path>
          <path
            class="path3"
            d="M16 9.806c-0.569 0-1.032 0.463-1.032 1.032v14.452c0 0.569 0.463 1.032 1.032 1.032s1.032-0.463 1.032-1.032v-14.452c0-0.569-0.463-1.032-1.032-1.032z"
          ></path>
          <path
            class="path4"
            d="M21.677 9.806c-0.569 0-1.032 0.463-1.032 1.032v14.452c0 0.569 0.463 1.032 1.032 1.032s1.032-0.463 1.032-1.032v-14.452c0-0.569-0.463-1.032-1.032-1.032z"
          ></path>
        </symbol>
        <symbol id="icon-clock" viewBox="0 0 32 32">
          <title>clock</title>
          <path
            class="path1"
            fill="#605f5f"
            d="M16 29c-7.168 0-13-5.831-13-13s5.832-13 13-13c7.168 0 13 5.832 13 13s-5.832 13-13 13zM16 0c-8.822 0-16 7.178-16 16s7.178 16 16 16c8.822 0 16-7.178 16-16s-7.178-16-16-16z"
          ></path>
          <path
            class="path2"
            fill="#605f5f"
            d="M23.958 21.837l-6.958-6.489v-6.282c0-0.827-0.673-1.5-1.5-1.5s-1.5 0.673-1.5 1.5v6.934c0 0.414 0.174 0.814 0.477 1.098l7.435 6.934c0.279 0.259 0.642 0.402 1.023 0.402 0.415 0 0.814-0.174 1.096-0.477 0.564-0.605 0.532-1.555-0.073-2.12z"
          ></path>
        </symbol>
      </defs>
    </svg>
    <!-- 内容 -->
    <div class="container">
      <div class="cart">
        <div class="page-title-normal">
          <h2 class="page-title-h2"><span>My Cart</span></h2>
        </div>
        <div class="item-list-wrap">
          <div class="cart-item">
            <div class="cart-item-head">
              <ul>
                <li>Items</li>
                <li>Price</li>
                <li>Quantity</li>
                <li>Subtotal</li>
                <li>Edit</li>
              </ul>
            </div>
            <ul class="cart-item-list">
              <li v-for="(item, index) in cartdata" :key="index">
                <div class="cart-tab-1">
                  <div class="cart-item-check">
                    <a
                      href="javascipt:vido(0);"
                      class="checkbox-btn item-check-btn"
                      :class="{ check: item.checked == '1' }"
                      @click="editCart('checked', item)"
                    >
                      <svg class="icon icon-ok">
                        <use xlink:href="#icon-ok"></use>
                      </svg>
                    </a>
                  </div>
                  <div class="cart-item-pic">
                    <img
                      :src="'../../static/img/' + item.productImage"
                      :title="item.productName"
                    />
                  </div>
                  <div class="cart-item-title">
                    <div class="item-name">{{ item.productName }}</div>
                  </div>
                </div>
                <div class="cart-tab-2">
                  <div class="item-price">{{ item.salePrice | currency('$')}}</div>
                </div>
                <div class="cart-tab-3">
                  <div class="item-quantity">
                    <div class="select-self select-self-open">
                      <div class="select-self-area">
                        <a class="input-sub" @click="editCart('minu', item)"
                          >-</a
                        >
                        <span class="select-ipt">{{ item.productNum }}</span>
                        <a class="input-add" @click="editCart('add', item)"
                          >+</a
                        >
                      </div>
                    </div>
                  </div>
                </div>
                <div class="cart-tab-4">
                  <div class="item-price-total">
                    {{ item.salePrice * item.productNum | currency('$')}}
                  </div>
                </div>
                <div class="cart-tab-5">
                  <div class="cart-item-opration">
                    <a
                      href="javascript:;"
                      class="item-edit-btn"
                      @click="delCartConfirm(item)"
                    >
                      <svg class="icon icon-del">
                        <use xlink:href="#icon-del"></use>
                      </svg>
                    </a>
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </div>
        <div class="cart-foot-wrap">
          <div class="cart-foot-inner">
            <div class="cart-foot-l">
              <div class="item-all-check">
                <a href="javascipt:vido(0);" @click="toggleCheckAll()">
                  <span class="checkbox-btn item-check-btn" :class="{'check':checkAll}">
                    <svg class="icon icon-ok">
                      <use xlink:href="#icon-ok" />
                    </svg>
                  </span>
                  <span>Select all</span>
                </a>
              </div>
            </div>
            <div class="cart-foot-r">
              <div class="item-total">
                Item total: <span class="total-price">{{totalPrice | currency('$')}}</span>
              </div>
              <div class="btn-wrap">
                <!-- 提交订单 -->
                <!-- 加上class:btn--dis按钮禁用 -->
                <!-- 当有商品选中就解禁,如果一个都没有选那么按钮会禁用 -->
                <a class="btn btn--red" :class="{'btn--dis':checkedCount==0}" @click="Checkout">Checkout</a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- 模态框 -->
    <Modal :mdShow="modelConfirm" v-on:close="closeModal()">
      <!--在父组件定义一个变量-->
      <h1 slot="message">是否要删除此订单?</h1>
      <div slot="btnGroup">
        <a href="#" class="btn btn--m" @click="delCart()">确定</a>
        <a href="#" class="btn btn--m" @click="modelConfirm = false">取消</a>
      </div>
    </Modal>

    <!-- 页脚 -->
    <Footer />
  </div>
</template>
<style>
.input-sub,
.input-add {
  min-width: 40px;
  height: 100%;
  border: 0;
  color: #605f5f;
  text-align: center;
  font-size: 16px;
  overflow: hidden;
  display: inline-block;
  background: #f0f0f0;
}
.item-quantity .select-self-area {
  background: none;
  border: 1px solid #f0f0f0;
}
.item-quantity .select-self-area .select-ipt {
  display: inline-block;
  padding: 0 3px;
  width: 30px;
  min-width: 30px;
  text-align: center;
}
</style>
<script>
// 引入css样式
import "../assets/css/checkout.css";
// 引入组件
import Header from "../components/Header";
import Footer from "../components/Footer";
import Bread from "../components/bread";
import Modal from "../components/Modal"; //引入模态框
import axios from "axios";
// 导入货币过滤器  {}:函数的导入方式用解构赋值接收
// 局部过滤器
// import {currency} from '../util/currency'
export default {
  data() {
    return {
      cartdata: [], //渲染数据
      modelConfirm: false, //模态框变量
      productId: "", //商品id
      // checkAll:false,   //全选按钮默认为不选
      delitem:''   //点击删除后的商品参数
    };
  },
  //  定义过滤器-过滤器可以是多个
  // filters:{
  //     currency:currency
  // },
  components: {
    Header,
    Footer,
    Bread,
    Modal,
  },
  computed: { //计算属性--如果每条商品都选中全选也会勾上,如果并不是每条商品都选中全选按钮是不会选中的
      // 思路:循环遍历购物车里的每条状态等于1,判断如果数据列表的长度等于每条状态的都选中的话--说明全选否则就不是全选
      checkedCount(){
          var i = 0;
          this.cartdata.forEach(item=>{
              if(item.checked=='1'){i++}
          })
          return i
      },
      //判断全选按钮是否选中
      checkAll(){
          return this.checkedCount==this.cartdata.length
      },
      // 计算选中商品的价格
      totalPrice(){
          var money = 0;
          this.cartdata.forEach(item=>{
            if(item.checked == '1'){  //等于1说明商品被选中了
                    //商品的价格*商品的数量
              money += Number(item.salePrice) * Number(item.productNum)
            }
          })
          return money
      }
  },
  mounted() {
    //在页面刚加载的时候渲染数据
    this.cartlist();
  },
  methods: {
    // 结算订单跳转页面
    Checkout(){
        if(this.checkedCount>0){
          this.$router.push({
            path:'/address'
          })
        }
    },
    //删除订单按钮
    delCartConfirm(item) {
      //点击删除按钮---弹出询问模态框
      this.modelConfirm = true;
      this.productId = item.productId; //赋值
      this.delitem = item
    },

    // 询问模态框确定删除按钮
    delCart() {
      axios
        .post("/users/cartDel", { productId: this.productId })
        .then((res) => {
          // console.log(res.data.result);
          if (res.data.status == "0") {
            this.closeModal();
            this.cartlist();
            //删除要删除的那一行的vuex里的商品的数量
            this.$store.commit('updatecartCount',-this.delitem.productNum)
          }
        });
    },

    //关闭模态框
    closeModal() {
      this.modelConfirm = false;
    },

    //获取购物车数据---渲染到页面
    cartlist() {
      axios.get("/users/cartList").then((res) => {
        // console.log(res.data.result)
        this.cartdata = res.data.result;
      });
    },

    //商品数量加减的方法
    editCart(flag, item) {
      if (flag == "add") {
        //如果flag==add就是加加
        item.productNum++;
      }
      if (flag == "minu") {
        //如果flag==minu就是减减
        if (item.productNum <= 1) {
          //如果数量小于等于1就不可以减了
          return;
        } else {
          item.productNum--;
        }
      }
      //如果flag==checked说明是添加class事件的函数
      if (flag == "checked") {
        //如果checked等于1默认就是1  那就添加选中class 否则就取消选中
        item.checked = item.checked == "1" ? "0" : "1";
        
      }
      axios
        .post("/users/cartEdit", {
          productId: item.productId,
          productNum: item.productNum,
          checked: item.checked,
        })
        .then((res) => {
            if(res.data.status=='0'){
                if(flag == "add"){
                  this.$store.commit('updatecartCount',1)
                }else if(flag == 'minu'){
                  this.$store.commit('updatecartCount',-1)
                }
            }
          
        });
    },

    //全选方法,全不选
    toggleCheckAll(){
      //AllFlag是个临时变量来接收checkAll的值,如果前面也用checkALL就会冲突,计算属性里已经计算过状态了
        this.AllFlag=!this.checkAll   //点击取反
        this.cartdata.forEach(item=>{
          item.checked=this.AllFlag   //这只是修改了前端的值,刷新就会消失
        })
        axios.post('/users/editCheckAll',{checkAll:this.checkAll}).then(
          res=>{
              return res
          }
        )
    }
  },
};
</script>



