<template>
  <div>
    <div class="md-overlay" v-show="price_isshow"></div>
    <!-- 头部 -->
    <Header />

    <!-- 面包屑  导航栏信息 -->
    <Bread>
      <span>Goods</span>
      <!-- <span slot="plice">plice</span> -->
    </Bread>

    <!-- 内容 -->
    <div class="accessory-result-page accessory-page">
      <div class="container">
        <div class="filter-nav">
          <span class="sortby">Sort by:</span>
          <a
            href="javascript:void(0)"
            class="default"
            :class="{ cur: curs === 'default' ? true : false }"
            @click="defaultfn"
            >Default</a
          >
          <a
            href="javascript:void(0)"
            class="price"
            @click="sortGoods()"
            :class="{ cur: curs === 'price' ? true : false }"
          >
            Price
            <svg class="icon icon-arrow-short">
              <use xlink:href="#icon-arrow-short" />
            </svg>
          </a>
          <a
            href="javascript:void(0)"
            class="filterby stopPop"
            @click="price_menu"
            >Filter by</a
          >
        </div>

        <!-- //价格列表 -->
        <div class="accessory-result">
          <!-- filter -->
          <div
            class="filter stopPop"
            id="filter"
            :class="{ 'filterby-show': price_left }"
          >
            <dl class="filter-price">
              <dt>Price:</dt>
              <dd>
                <a
                  href="javascript:void(0)"
                  :class="{ cur: clicked === 'active' }"
                  @click="changeTab('active')"
                  >All</a
                >
              </dd>
              <dd
                v-for="(item, index) in Price"
                :key="index"
                :class="{ active: clicked == index }"
                @click="changeTab(index)"
              >
                <a href="javascript:void(0)"
                  >{{ item.startPrice }} - {{ item.endPrice }}</a
                >
              </dd>
            </dl>
          </div>

          <!-- search result accessories list -->
          <div class="accessory-list-wrap">
            <div class="accessory-list col-4">
              <ul>
                <!-- //商品列表 -->
                <li v-for="(item, index) in goodlist" :key="index">
                  <div class="pic">
                    <a href="#">
                      <img
                        v-lazy="'../../static/img/' + item.productImage"
                        alt
                      />
                      <!-- v-lazy:懒加载 -->
                    </a>
                  </div>
                  <div class="main">
                    <div class="name">{{ item.productName }}</div>
                    <div class="price">{{ item.salePrice }}</div>
                    <div class="btn-area">
                      <a
                        href="javascript:;"
                        class="btn btn--m"
                        @click="addCart(item.productId)"
                        >加入购物车</a
                      >
                    </div>
                  </div>
                </li>
              </ul>
              <div
                v-infinite-scroll="loadMore"
                infinite-scroll-disabled="busy"
                infinite-scroll-distance="10"
              >
                <img
                  v-show="loadingSVG"
                  src="../../static/img/loading-svg/loading-balls.svg"
                  alt=""
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- 没有登陆点击购物车出现的模态框 -->
    <!--子组件自定义一个事件close传到了父组件   父组件接收然后定义一个方法把mdShow变成false-->
    <Modal :mdShow="mdShow" v-on:close="closeModal()">
      <!--在父组件定义一个变量-->
      <h1 slot="message">请先登录再购物...</h1>
      <div slot="btnGroup">
        <a href="#" class="btn btn--m" @click="closeModal()">关闭</a>
      </div>
    </Modal>

    <!-- 加入购物车弹出的模态框 -->
    <Modal :mdShow="mdShowCart" v-on:close="closeModal()">
      <!--在父组件定义一个变量-->
      <h1 slot="message">添加购物车成功!!!</h1>
      <div slot="btnGroup">
        <a href="#" class="btn btn--m" @click="mdShowCart = false">继续购物!</a>
        <router-link class="btn btn--m" to="/cart">查看购物车</router-link>
      </div>
    </Modal>

    <!-- 底部信息 -->
    <Footer />
  </div>
</template>

<script>
// 引入css样式
import "../assets/css/base.css";
import "../assets/css/login.css";
import "../assets/css/product.css";
// 引入分好的组件
import Header from "../components/Header";
import Footer from "../components/Footer";
import Bread from "../components/bread";
import Modal from "../components/Modal"; //引入模态框
import axios from "axios";
export default {
  data() {
    return {
      mdShowCart: false, //添加购物车弹出的控制模态框的变量
      mdShow: false, //模态框定义的参数---false隐藏   true显示  默认时false
      goodlist: [], //存放商品列表数据
      Price: [
        //自定义的左侧价格列表
        { startPrice: "0.00", endPrice: "100" },
        { startPrice: "100", endPrice: "1000" },
        { startPrice: "1000", endPrice: "2000" },
        { startPrice: "2000", endPrice: "6000" },
        { startPrice: "6000", endPrice: "10000" },
      ],
      clicked: 0, //切换点击样式时用的参数
      price_left: false, //左侧商品价值是否显示
      price_isshow: false, //遮罩层是否显示
      page: 1, //起始页
      pageSize: 4, //每页有几条数据
      sortElag: false, //升序还是降序
      busy: true, //分页变量 true:禁止   false:为允许
      loadingSVG: false, //控制加载中的图片啥时候显示
      curs: "default", //默认选中的是default
    };
  },
  components: {
    Header,
    Footer,
    Bread,
    Modal,
  },
  mounted() {
    // 生命周期函数---在页面一开始就调用请求数据的函数
    this.getGoodslist();
  },
  methods: {
    //关闭模态框的方法
    closeModal() {
      //自定义事件--把父组件里的mdShow变成false
      this.mdShow = false;
      this.mdShowCart=false
    },

    // 滚动加载的方法
    loadMore() {
      setTimeout(() => {
        this.page++; //需要添加数据所以加加
        this.getGoodslist(true); //传递的参数true是没有busy禁止
      }, 1500);
    },

    // 请求数据的方法
    getGoodslist(flag) {
      this.loadingSVG = true; //调用方法的时候显示svg图片
      var param = {
        //定义系统列表的参数,默认从第一页开始
        price: this.clicked, //价格列表的下标,按照这个下标查询对应的价格
        page: this.page, //页数
        pageSize: this.pageSize, //每页显示几条数据
        sort: this.sortElag ? 1 : -1, //升序或降序
      };
      //改为子路由---让中间件更准确的限制
      axios.get("/goods/list", { params: param }).then((res) => {
        if (res.data.status == 0) {
          //请求成功的状态 成功为0   失败是1
          if (flag) {
            //如果鼠标没有滚动,这个变量是undefind,运行else
            this.goodlist = this.goodlist.concat(res.data.result.list); //页面滚动往goodlist里添加数据,而不是替换数据
            if (res.data.result.count == 0) {
              //count是数据的条数,如果为0说明没数据了
              this.busy = true; //没数据了就禁止
              this.loadingSVG = false; //没有数据后隐藏图片
            } else {
              this.busy = false;
            }
          } else {
            // 请求成功把数据赋给data里定的值,然后渲染数据
            this.goodlist = res.data.result.list;
            this.busy = false; //可以加载
          }
        } else {
          this.goodlist = [];
        }
      });
    },
    defaultfn() {
      this.curs = "default"; //把样式改成default上
    },

    // 点击升序降序
    sortGoods() {
      this.curs = "price"; //把样式改成price上
      this.sortElag = !this.sortElag; //true为升序---false降序  点击的时候取反
      this.page = 1;
      this.getGoodslist();
    },

    // 添加点击样式
    changeTab(index) {
      // console.log(index)
      this.clicked = index;
      this.price_left = false; //左侧价格隐藏
      this.price_isshow = false; //遮罩层隐藏
      this.page = 1; //点击保证页面从第一页开始
      this.getGoodslist(); //调用获取数据的方法
    },

    // 点击price的时候---在页面缩小的时候
    price_menu() {
      this.price_left = true; //左侧价格显示
      this.price_isshow = true; //遮罩层显示
    },

    // 加入购物车方法
    addCart(productId) {
      axios.post("/goods/addCart", { productId: productId }).then((res) => {
        // alert(res.data.status=='0'?'商品添加成功':"商品添加失败:"+res.data.status)
        // res.data.status == "0"
        //   ? (this.mdShowCart = true)
        //   : (this.mdShow = true); //如果没登录商品添加失败把mdShow变为true---模态框显示
        if(res.data.status=='0'){
            this.mdShowCart = true
            // 加入购物车的时候vuex的商品数量加一
            this.$store.commit('updatecartCount',1)
        }else{
            this.mdShow = true
        }
      });
    },
  },
};
</script>

<style>
.active {
  color: orangered;
}
</style>


