<template>
  <div>
    <!-- 头部 -->
    <Header />
    
    <Bread>
      <span>orderSuccess</span>
    </Bread>

    <div class="container">
      <div class="page-title-normal">
        <h2 class="page-title-h2"><span>check out</span></h2>
      </div>
      <!-- 进度条 -->
      <div class="check-step">
        <ul>
          <li class="cur"><span>Confirm</span> address</li>
          <li class="cur"><span>View your</span> order</li>
          <li class="cur"><span>Make</span> payment</li>
          <li class="cur"><span>Order</span> confirmation</li>
        </ul>
      </div>

      <div class="order-create">
        <div class="order-create-pic">
          <img src="../../static/img/ok-2.png" alt="" />
        </div>
        <div class="order-create-main">
          <h3>Congratulations! <br />Your order is under processing!</h3>
          <p>
            <span>Order ID：{{orderId}}</span>
            <span>Order total：{{orderTotal}}</span>
          </p>
          <div class="order-create-btn-wrap">
            <div class="btn-l-wrap">
              <router-link class="btn btn--m" to="/cart">Cart List</router-link>
            </div>
            <div class="btn-r-wrap">
              <router-link class="btn btn--m" to="/">Goods List</router-link>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 页脚 -->
    <Footer />
  </div>
</template>
<script>
import Header from "../components/Header"; //头部
import Footer from "../components/Footer"; //页脚
import Bread from "../components/bread"; //面包屑
import Modal from "../components/Modal"; //引入模态框
import axios from "axios";
export default {
  data() {
    return {
        orderId:'',
        orderTotal:''
    }
  },
  components: {
    Header,
    Footer,
    Bread,
    Modal
  },
  mounted() {
    this.init()
    this.delinit()
  },
  methods: {
      init(){
      this.orderId=this.$route.query.orderId
      this.orderTotal=this.$route.query.orderTotal
      console.log(this.orderId,this.orderTotal)
    },

    //删除选中的商品
    delinit(){
        axios.get('/users/delinit').then(
          res=>{
              if(res.data.status=='0'){
                console.log(res.data.result)
              }
          }
        )
    }
  }
    
};
</script>



