<template>
  <div>
    <!-- 头部 -->
    <Header />

    <!-- 面包屑 导航信息-->
    <Bread>
      <span>Address</span>
    </Bread>

    <div class="checkout-page">
      <svg
        style="position: absolute; width: 0; height: 0; overflow: hidden"
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
        xmlns:xlink="http://www.w3.org/1999/xlink"
      >
        <defs>
          <symbol id="icon-add" viewBox="0 0 31 32">
            <title>add</title>
            <path
              class="path1"
              d="M30.745 15.152h-14.382v-14.596c0-0.308-0.243-0.557-0.543-0.557s-0.543 0.249-0.543 0.557v14.596h-14.665c-0.3 0-0.543 0.249-0.543 0.557s0.243 0.557 0.543 0.557h14.665v15.177c0 0.307 0.243 0.557 0.543 0.557s0.543-0.249 0.543-0.557v-15.177h14.382c0.3 0 0.543-0.249 0.543-0.557s-0.243-0.557-0.543-0.557z"
            ></path>
          </symbol>
          <symbol id="icon-ok" viewBox="0 0 32 32">
            <title>ok</title>
            <path
              class="path1"
              d="M14.084 20.656l-7.845-9.282c-1.288-1.482-3.534-1.639-5.016-0.351s-1.639 3.534-0.351 5.016l10.697 12.306c1.451 1.669 4.057 1.623 5.448-0.096l18.168-22.456c1.235-1.527 0.999-3.765-0.528-5.001s-3.765-0.999-5.001 0.528l-15.573 19.337z"
            ></path>
          </symbol>
          <symbol id="icon-edit" viewBox="0 0 32 32">
            <title>edit</title>
            <path
              class="path1"
              d="M28.287 8.51l-4.805-4.806 0.831-0.831c0.472-0.472 1.086-0.777 1.564-0.777 0.248 0 0.452 0.082 0.622 0.253l3.143 3.144c0.539 0.54 0.133 1.529-0.524 2.186l-0.831 0.831zM26.805 9.992l-1.138 1.138-4.805-4.806 1.138-1.138 4.805 4.806zM24.186 12.612l-14.758 14.762-4.805-4.806 14.758-14.762 4.805 4.806zM7.379 28.288l-4.892 1.224 1.223-4.894 3.669 3.67zM31.123 4.011l-3.143-3.144c-0.567-0.567-1.294-0.867-2.103-0.867-1.036 0-2.174 0.52-3.045 1.391l-20.429 20.436c-0.135 0.134-0.23 0.302-0.276 0.487l-2.095 8.385c-0.089 0.355 0.017 0.736 0.276 0.995 0.198 0.198 0.461 0.307 0.741 0.307 0.085 0 0.171-0.010 0.254-0.031l8.381-2.096c0.185-0.047 0.354-0.142 0.487-0.276l20.43-20.436c1.409-1.41 2.042-3.632 0.524-5.15v0z"
            ></path>
          </symbol>
          <symbol id="icon-del" viewBox="0 0 32 32">
            <title>delete</title>
            <path
              class="path1"
              d="M11.355 4.129v-2.065h9.29v2.065h-9.29zM6.194 29.935v-23.742h19.613v23.742h-19.613zM30.968 4.129h-8.258v-3.097c0-0.569-0.463-1.032-1.032-1.032h-11.355c-0.569 0-1.032 0.463-1.032 1.032v3.097h-8.258c-0.569 0-1.032 0.463-1.032 1.032s0.463 1.032 1.032 1.032h3.097v24.774c0 0.569 0.463 1.032 1.032 1.032h21.677c0.569 0 1.032-0.463 1.032-1.032v-24.774h3.097c0.569 0 1.032-0.463 1.032-1.032s-0.463-1.032-1.032-1.032v0z"
            ></path>
            <path
              class="path2"
              d="M10.323 9.806c-0.569 0-1.032 0.463-1.032 1.032v14.452c0 0.569 0.463 1.032 1.032 1.032s1.032-0.463 1.032-1.032v-14.452c0-0.569-0.463-1.032-1.032-1.032z"
            ></path>
            <path
              class="path3"
              d="M16 9.806c-0.569 0-1.032 0.463-1.032 1.032v14.452c0 0.569 0.463 1.032 1.032 1.032s1.032-0.463 1.032-1.032v-14.452c0-0.569-0.463-1.032-1.032-1.032z"
            ></path>
            <path
              class="path4"
              d="M21.677 9.806c-0.569 0-1.032 0.463-1.032 1.032v14.452c0 0.569 0.463 1.032 1.032 1.032s1.032-0.463 1.032-1.032v-14.452c0-0.569-0.463-1.032-1.032-1.032z"
            ></path>
          </symbol>
          <symbol id="icon-clock" viewBox="0 0 32 32">
            <title>clock</title>
            <path
              class="path1"
              d="M29.333 16c0-7.364-5.97-13.333-13.333-13.333s-13.333 5.97-13.333 13.333c0 7.364 5.97 13.333 13.333 13.333s13.333-5.97 13.333-13.333v0 0 0 0 0 0zM0 16c0-8.837 7.163-16 16-16s16 7.163 16 16c0 8.837-7.163 16-16 16s-16-7.163-16-16zM14.667 14.667v1.333h2.667v-10.667h-2.667v9.333zM24 18.667h1.333v-2.667h-10.667v2.667h9.333z"
            ></path>
          </symbol>
          <symbol id="icon-question" viewBox="0 0 32 32">
            <title>question</title>
            <path
              class="path1"
              d="M16 2.56c7.411 0 13.44 6.029 13.44 13.44s-6.029 13.44-13.44 13.44c-7.411 0-13.44-6.029-13.44-13.44s6.029-13.44 13.44-13.44zM16 0c-8.822 0-16 7.178-16 16s7.178 16 16 16c8.822 0 16-7.178 16-16s-7.178-16-16-16z"
            ></path>
            <path
              class="path2"
              d="M16 22.080c-1.059 0-1.92 0.861-1.92 1.92s0.861 1.92 1.92 1.92c1.059 0 1.92-0.861 1.92-1.92s-0.861-1.92-1.92-1.92z"
            ></path>
            <path
              class="path3"
              d="M12.16 12.48c0.706 0 1.28-0.574 1.28-1.28 0-1.412 1.148-2.56 2.56-2.56s2.56 1.148 2.56 2.56c0 1.412-1.148 2.56-2.56 2.56-0.706 0-1.28 0.574-1.28 1.28v3.84c0 0.706 0.574 1.28 1.28 1.28s1.28-0.574 1.28-1.28v-2.723c2.224-0.575 3.84-2.616 3.84-4.957 0-2.823-2.297-5.12-5.12-5.12s-5.12 2.297-5.12 5.12c0 0.706 0.574 1.28 1.28 1.28z"
            ></path>
          </symbol>
        </defs>
      </svg>
      <div class="container">
        <div class="checkout-addr">
          <div class="page-title-normal">
            <h2 class="page-title-h2"><span>check out</span></h2>
          </div>
          <!-- process step -->
          <div class="check-step">
            <ul>
              <li class="cur"><span>Confirm</span> address</li>
              <li><span>View your</span> order</li>
              <li><span>Make</span> payment</li>
              <li><span>Order</span> confirmation</li>
            </ul>
          </div>

          <!-- address list -->
          <div class="page-title-normal checkout-title">
            <h2><span>Shipping address</span></h2>
          </div>
          <div class="addr-list-wrap">
            <div class="addr-list">
              <ul>
                <!-- 默认显示三条数据 -->
                <!-- 设置默认地址 点击哪个把哪个设置成默认收货地址-->
                <li
                  v-for="(item, index) in addressListFlag"
                  :key="index"
                  :class="{ check: selectAddrId == item.addressId }"
                  @click="change(item)"
                >
                  <dl>
                    <dt>{{ item.userName }}</dt>
                    <dd class="address">{{ item.streetName }}</dd>
                    <dd class="tel">{{ item.tel }}</dd>
                  </dl>
                  <!-- 删除地址 -->
                  <div class="addr-opration addr-del">
                    <a
                      href="javascript:;"
                      class="addr-del-btn"
                      @click="delAddressFlag(item.addressId)"
                    >
                      <svg class="icon icon-del">
                        <use xlink:href="#icon-del"></use>
                      </svg>
                    </a>
                  </div>
                  <div class="addr-opration addr-set-default">
                    <!--item.idDefault等于true的话,设置成默认地址-->
                    <a
                      href="javascript:;"
                      class="addr-set-default-btn"
                      v-if="item.isDefault"
                      ><i>Default address</i></a
                    >
                  </div>
                  <!-- 不等于true设置成set default -->
                  <div
                    class="addr-opration addr-default"
                    v-if="!item.isDefault"
                    @click="Setdefault(item.addressId)"
                  >
                    Set default
                  </div>
                </li>

                <!-- 添加新地址 -->
                <li class="addr-new">
                  <div class="add-new-inner">
                    <i class="icon-add" @click="isAdd = true">
                      <svg class="icon icon-add">
                        <use xlink:href="#icon-add"></use>
                      </svg>
                    </i>
                    <p>Add new address</p>
                  </div>
                </li>
              </ul>
            </div>

            <div class="shipping-addr-more">
              <!-- 点击显示更多数据 -->
              <a
                class="addr-more-btn up-down-btn"
                href="javascript:;"
                @click="expand"
                :class="{ open: limit > 3 }"
              >
                more
                <i class="i-up-down">
                  <i class="i-up-down-l"></i>
                  <i class="i-up-down-r"></i>
                </i>
              </a>
            </div>
          </div>

          <!-- shipping method-->
          <div class="page-title-normal checkout-title">
            <h2><span>Shipping method</span></h2>
          </div>
          <div class="shipping-method-wrap">
            <div class="shipping-method">
              <ul>
                <li class="check">
                  <div class="name">Standard shipping</div>
                  <div class="price">Free</div>
                  <div class="shipping-tips">
                    <p>
                      Once shipped，Order should arrive in the destination in
                      1-7 business days
                    </p>
                  </div>
                </li>
              </ul>
            </div>
          </div>
          <div class="next-btn-wrap">
            <!-- <a class="btn btn--m btn--red" href="#">Next</a> -->
            <router-link
              class="btn btn--m btn--red"
              :to="url"
              @click.native="Next"
              >Next</router-link
            >
          </div>
        </div>
      </div>
    </div>

    <!-- 模态框 -->
    <Modal v-on:close="closeModal()" :mdShow="isMdShow">
      <!--在父组件定义一个变量-->
      <h1 slot="message">是否要删除此地址?</h1>
      <div slot="btnGroup">
        <a href="#" class="btn btn--m" @click="delAddress">确定</a>
        <a href="#" class="btn btn--m" @click="isMdShow = false">取消</a>
      </div>
    </Modal>

    <!-- 添加新地址出现的模态框 -->
    <Modal v-on:close="closeAddModal()" :mdShow="isAdd" class="addAddress">
      <p slot="message">
        <label for="">添加您的新地址:</label>
        <input type="text" placeholder="请输入姓名" v-model="name" />
        <input type="text" placeholder="请输入地址" v-model="address" />
        <input type="text" placeholder="请输入电话" v-model="tel" />
        <input type="text" placeholder="请输入邮政编码" v-model="postCode" />
      </p>
      <div slot="btnGroup">
        <a href="#" class="btn btn--m" @click="addDefine">确定</a>
        <a href="#" class="btn btn--m" @click="isAdd = false">取消</a>
      </div>
    </Modal>
    <!-- 页脚 -->
    <Footer />
  </div>
</template>
<style>
</style>
<script>
import Header from "../components/Header"; //头部
import Footer from "../components/Footer"; //页脚
import Bread from "../components/bread"; //面包屑
import Modal from "../components/Modal"; //引入模态框
import axios from "axios";

export default {
  data() {
    return {
      url: "",
      addressList: [], //存放数据
      limit: 3, //默认显示的条数
      checkedIndex: 0, //默认地址选中的索引
      isMdShow: false, //模态框--默认不显示
      selectAddrId: "",
      isAdd: false, //添加地址模态框的状态
      name: "", //姓名
      address: "", //地址
      tel: "", //电话
      postCode: "", //邮政编码
    };
  },
  components: {
    Header,
    Footer,
    Bread,
    Modal,
  },
  computed: {
    addressListFlag() {
      //定义显示几条数据,
      return this.addressList.slice(0, this.limit);
    },
  },
  mounted() {
    this.init();
  },
  methods: {
    //确定修改地址的方法
    addDefine() {
      var userName = this.name;
      var streetName = this.address;
      var tel = this.tel;
      var postCode = this.postCode;
      var addressId = new Date().getTime() + Math.floor(Math.random() * 999);
      var isDefault = false;
      axios
        .post("/users/addDefine", {
          userName,
          streetName,
          tel,
          postCode,
          addressId,
          isDefault,
        })
        .then((res) => {
          if (res.data.status == "0") {
            // console.log(res.data.result);
            this.isAdd = false;
            this.init();
            this.limit=3;
            this.name = "";
            this.address = "";
            this.tel = "";
            this.postCode = "";
          }
        });
    },
    //改变地址选中的样式
    change(item) {
      this.selectAddrId = item.addressId;
      this.url = {
        path: "/orderConfim",
        query: { addressId: this.selectAddrId },
      };
    },
    Next() {
      //如果没选默认地址就不能跳转页面
      if (this.selectAddrId == "") {
        this.url = "";
        alert("请先输入收货地址");
      }
    },
    // 请求数据渲染页面
    init() {
      axios.get("/users/addressList").then((res) => {
        if (res.data.status == "0") {
          this.addressList = res.data.result;
          this.selectAddrId = "";
          this.url = "";
          this.addressList.forEach((item) => {
            if (item.isDefault) {
              //判断默认地址是否选中--
              this.selectAddrId = item.addressId; //如果选中就可以跳转页面
              this.url = {
                path: "/orderConfim",
                query: { addressId: this.selectAddrId }
              };
            }
          });
        }
      });
    },

    //点击显示更多的数据
    expand() {
      if (this.limit == 3) {
        //说明默认显示三条
        this.limit = this.addressList.length;
      } else {
        this.limit = 3;
      }
    },

    //点击修改默认地址
    Setdefault(addressId) {
      console.log(addressId);
      axios.post("/users/setAddress", { addressId: addressId }).then((res) => {
        if (res.data.status == "0") {
          console.log(res.data.result);
          this.init();
        }
      });
    },

    // 删除地址,弹出模态框
    delAddressFlag(addressId) {
      //模态框显示
      this.isMdShow = true;
      this.addressId = addressId;
    },
    //关闭模态框
    closeModal() {
      this.isMdShow = false;
    },
    closeAddModal() {
      this.isAdd = false;
    },
    //删除地址
    delAddress() {
      axios
        .post("/users/delAddres", { addressId: this.addressId })
        .then((res) => {
          if (res.data.status == "0") {
            this.isMdShow = false;
            return res;
            
          }
        });
      this.init();
    },
  },
};
</script>
<style>
.addAddress input {
  width: 100%;
  height: 50px;
  margin-top: 10px;
}
</style>



