<template>
  <!--模态框-->
  <!-- 添加class:md-show 实现隐藏或显示 -->
  <div>
    <div
      class="md-modal modal-msg md-modal-transition"
      :class="{ 'md-show': mdShow }"
    >
      <div class="md-modal-inner">
        <div class="md-top">
          <div class="md-title">Login in</div>
          <button class="md-close" @click="closeModal()">Close</button>
        </div>
        <div class="md-content">
          <div class="confirm-tips">
            <!-- 插槽 -->
            <slot name="message"></slot>
          </div>
          <!-- 按钮 -->
          <div class="btn-wrap">
                <slot name="btnGroup"></slot>
          </div>
        </div>
      </div>
    </div>
    <!-- 遮罩层 -->
    <div class="md-overlay" v-show="mdShow" @click="closeModal()"></div>
  </div>
</template>>
       


<script>
export default {
  props:['mdShow'],    //这个变量是从父组件Goodlist传递过来的---子组件没办法修改参数
  data() {
    return {
      
    }
  },
  methods: {
    closeModal(){
      this.$emit('close')   //向父组件传递一个自定义事件
    }
  },
};
</script>

<style>

</style>>



