// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import lazyload from 'vue-lazyload'  //懒加载
import infiniteScroll from 'vue-infinite-scroll'//滚动插件
import {currency} from './util/currency'  //全局引入过滤器
import Vuex from 'vuex'

Vue.use(Vuex)//使用vuex
Vue.use(lazyload,{
  loading:"../static/img/loading-svg/loading-spin.svg"   //加载动画
})
Vue.use(infiniteScroll)//使用插件
Vue.filter('currency',currency)

// vuex
const store = new Vuex.Store({
    state:{
      nikeName:'',    //登录名称
      cartCount:0    //购物车数量
    },
    mutations:{   //通过它改变state的状态
        updateUser(state,nikeName){
            state.nikeName=nikeName
        },
        updatecartCount(state,cartCount){  //更改购物车的数量
            state.cartCount+=cartCount
        },
        initCount(state,cartCount){
            state.cartCount=cartCount
        }
    }
})
Vue.config.productionTip = false


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



