import Vue from 'vue'
import Router from 'vue-router'
import Goodlist from '@/view/Goodlist.vue' //引入商品页面
import Cart from '@/view/Cart.vue'  //引入购物车页面
import Address from '@/view/Address' //引入结算页面
import orderConfim from '../view/OrderConfirm.vue' 
import orderSuccess from '../view/OrderSuccess.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Goodlist',
      component: Goodlist
    },
    {
      path:'/cart',
      name:'Cart',
      component:Cart
    },
    {
      path:'/address',
      name:'Address',
      component:Address
    },
    {
      path:'/orderConfim',
      name:'orderConfim',
      component:orderConfim
    },{
      path:'/orderSuccess',
      name:'orderSuccess',
      component:orderSuccess
    }
  ]
})



